.hero-image {
    background-image: url("../../assets/arco.jpg");
    height: 800px;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;



  }
  
  .hero-text {
    text-align: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color:white;
    font-family: 'nunito','sans serif';
    font-weight: 800;
    font-size: 24px;    
    text-shadow: 20px 20px 50px black, 0 0 75px blue, 0 0 15px darkblue;
 

  }

  .hero-title {
    font-family: 'nunito','sans serif';
    font-size: 60px;
    font-weight: 800;
    text-shadow: 3px 3px 50px black, 0 0 50px blue, 0 0 15px darkblue;
  
  }